
import { auth, profilesCollection, getTimelineCollection } from '@/firebase.config'
import router from '@/router'
import { reactive, toRefs, computed } from 'vue'
import ContentCard from '@/components/shared/ContentCard'
import EditableTextInput from '@/components/shared/EditableTextInput'
import { UserProfile } from '../types/UserProfile'
import { invertHex } from '@/utils/utils'
import deleteContentCard from '@/services/deleteContentCard'
import usePatchMeta from '@/components/shared/usePatchMeta'

export default {
    components: {
        ContentCard,
        EditableTextInput,
        usePatchMeta
    },
    props: {
        userUid: String,
        postUid: String,
    },
    async setup (props) {
        const isOwnProfile = auth?.currentUser?.uid === props.userUid
        
        const postRef = getTimelineCollection(props.userUid).doc(props.postUid)
        const userRef = profilesCollection.doc(props.userUid)

        const [postDoc, userDoc] = await Promise.all([ postRef.get(), userRef.get() ])
        if (!postDoc?.exists || !userDoc?.exists) {
            console.log('Post or user not found, TODO 404')
            router.replace('/404')
            return
        }
        const postData: BaseContentCard = postDoc.data() as BaseContentCard
        const userData: UserProfile = userDoc.data() as UserProfile
        const reactiveData = reactive({
            isOwnProfile,
            contentCard: postData,
            userData,
            editMode: false
        })
        //Keep updating card template for own user profile
        if (isOwnProfile) {
            postRef.onSnapshot(async (postDoc) => {
                const data = postDoc.data() as BaseContentCard
                if (!data) return
                reactiveData.contentCard = postDoc.data() as BaseContentCard
            })
        }

        const previewUrl = userData?.previewVideo || userData?.previewImage || userData?.iFrameUrl

        // eslint-disable-next-line @typescript-eslint/no-use-before-define
        const invertedHex = computed(() => invertHex(userData.bgColor))
       
        const onDelete = async () => {
            if (await deleteContentCard(props.postUid, reactiveData?.contentCard?.hostedFileName)) {
                router.replace('/')
            }
        }

        return {
            ...toRefs(reactiveData),
            invertedHex,
            onDelete,
            previewUrl
        }
    }
}
